














































































































var view;

export default {
	data() {
		return {
			areaList:[],
			streetList:[],
			communityList:[],
			customerDoorNo:'',
			customerName:'',
			customerArea:'',
			customerAreaName:'',
			customerCommunity:'',
			customerCommunityName:'',
			customerStreet:'',
			customerStreetName:'',
			customerAdd:'',
			customerPhone:'',
			remark:'',
			safetyEndTime:'',
			dangerList:[],
			dangerNum:0,
			scBillno:'',
			isTeach:'',
		};
	},
	methods:{
		areaChange(e){
			let index = e.detail.value;
			if( this.areaList.length > index &&  this.customerArea != this.areaList[index].id){
				this.customerAreaName = this.areaList[index].areaName;
				this.customerArea = this.areaList[index].id;
				findStreet();
			}
		},
		streetChange(e){
			let index = e.detail.value;
			if(this.customerStreet.length > index && this.customerStreet != this.streetList[index].id){
				this.customerStreetName = this.streetList[index].streetName;
				this.customerStreet = this.streetList[index].id;
				findCommunityRepository();
			}
		},
		communityChange(e){
			let index = e.detail.value;
			console.log(index);
			if(this.communityList.length > index && this.customerCommunity != this.communityList[index].id){
				this.customerCommunityName = this.communityList[index].communityName;
				this.customerCommunity = this.communityList[index].id;
			}
		},
		dateChange(e){
			this.safetyEndTime = e.target.value;
		},
		scanCode: function () {
			uni.scanCode({
				success: function (res) {
					view.customerDoorNo = res.result;
				},
				fail: function (res) {}
			})
		},
		itemClick(child){
			child.checked = !child.checked;
			if(child.checked){
				this.dangerNum++;
			}else{
				this.dangerNum--;
			}
		}
	},
	mounted() {
		view = this;
		getDangersList();
		findArea();
	}
}

function getDangersList(){
	
	view.$request({
		url:'FINDNORMALCUSTOMERSAFETYTASKDANGERS',
		success(data) {
			view.dangerList = data.map( (item,index) => {
				item.show = index === 0;
				item.safetyDangers.forEach(item => item.checked = false);
				return item;
			});
		},
	})
}

function findArea(){
	
	view.$request({
		url:'FINDAREA',
		success(data) {
			view.areaList = data || [];
		},
	})
}

function findStreet(){
	
	view.$request({
		url:'FINDSTREET',
		method:'POST',
		data:{
			areaId:view.customerArea
		},
		success(data) {
			view.streetList = data || [];
		},
	})
}

function findCommunityRepository(){
	
	view.$request({
		url:'FINDCOMMUNITYREPOSITORY',
		method:'POST',
		data:{
			streetId:view.customerStreet
		},
		success(data) {
			view.communityList = data || [];
		},
	})
}

